"use client"

import React, { useState } from 'react'
import { useForm, SubmitHandler } from 'react-hook-form'
import { Input } from '@/components/ui/input'
import { Button } from '@/components/ui/button'

type FormValues = {
  username: string
  password: string
}

export function LoginForm() {
  const [isLoading, setIsLoading] = useState(false)
  const [loginError, setLoginError] = useState('')

  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm<FormValues>()

  const onSubmit: SubmitHandler<FormValues> = async (data) => {
    try {
      setIsLoading(true)
      setLoginError('')
      
      // Simulate login process
      console.log('Attempting login with:', data)
      
      // Simulate API delay
      await new Promise(resolve => setTimeout(resolve, 1000))
      
      // Simulate login failure for demo purposes
      if (data.password === 'fail') {
        throw new Error('Sorry, your password was incorrect. Please double-check your password.')
      }
      
      // Simulate success
      alert('Login successful! (This is a demo)')
      
    } catch (error: any) {
      setLoginError(error.message || 'Login failed. Please try again.')
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="w-full">
      <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
        <div>
          <Input
            id="username"
            placeholder="Phone number, username, or email"
            {...register('username', { 
              required: 'Phone number, username, or email is required',
              minLength: {
                value: 1,
                message: 'This field is required'
              }
            })}
            className="w-full h-10 px-3 text-sm bg-gray-50 border border-gray-300 rounded-sm focus:border-gray-400 focus:bg-white"
            disabled={isLoading}
          />
          {errors.username && (
            <p className="mt-1 text-xs text-red-500">{errors.username.message}</p>
          )}
        </div>

        <div>
          <Input
            id="password"
            type="password"
            placeholder="Password"
            {...register('password', { 
              required: 'Password is required',
              minLength: {
                value: 1,
                message: 'This field is required'
              }
            })}
            className="w-full h-10 px-3 text-sm bg-gray-50 border border-gray-300 rounded-sm focus:border-gray-400 focus:bg-white"
            disabled={isLoading}
          />
          {errors.password && (
            <p className="mt-1 text-xs text-red-500">{errors.password.message}</p>
          )}
        </div>

        {loginError && (
          <div className="text-xs text-red-500 text-center">
            {loginError}
          </div>
        )}

        <Button 
          type="submit" 
          className="w-full h-8 bg-blue-500 hover:bg-blue-600 text-white text-sm font-semibold rounded-sm disabled:opacity-50"
          disabled={isLoading}
        >
          {isLoading ? 'Logging in...' : 'Log in'}
        </Button>
      </form>

      <div className="flex items-center my-4">
        <div className="flex-1 border-t border-gray-300"></div>
        <span className="px-4 text-xs text-gray-500 font-semibold uppercase">OR</span>
        <div className="flex-1 border-t border-gray-300"></div>
      </div>

      <div className="text-center">
        <button className="text-sm text-blue-900 font-semibold">
          Log in with Facebook
        </button>
      </div>

      <div className="text-center mt-4">
        <a href="#" className="text-xs text-blue-900">
          Forgot password?
        </a>
      </div>
    </div>
  )
}
